.friens {
  width: 264px;
  margin: 0 auto;
  list-style: none;
}
.friendItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 260px;
  margin: 4px;
  border: 1px solid black;
  background-color: rgb(218, 167, 175);
}
.avatar {
  left: 20px;
  width: 100px;
  padding: 16px;
}
.name {
  padding-right: 20px;
  font-size: 26px;

}
.noactive {
  width: 14px;
  height: 14px;
  background-color: red;
  border-radius: 50%;
  margin-left: 10px;
}

.active {
  width: 14px;
  height: 14px;
  background-color: green;
  border-radius: 50%;
  margin-left: 10px;
}