.statistics {
  width: 600px;
  margin: 0 auto;
  text-align: center;
  font-size: 26px;
  background-color: rgb(243, 180, 180);
}

.statList {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: 0;
}
.statsItem {
  width: 20em; 
  border: 1px solid gray;
  background-color: rgb(250, 231, 231);
}
.label {
display: block;
padding: 14px;
}