.profile {
  width: 400px;
  font-size: 20px;
  font-weight: 500;
 margin: 0 auto;
 text-align: center;
}

.description {
  background-color: rgb(221, 216, 216);
  margin: 0;
}

.avatar {
  width: 260px;
  border-radius: 50%;
  background-color: white;
  margin-top: 14px;
}

.location {
  margin-bottom: 0;
  padding-bottom:30px;
}

.stats {
  display: flex;
  justify-content: space-around;
  font-weight: 700;
  height: 100px;
  list-style: none;
  padding: 0;
  margin: 0;
  background-color: rgb(158, 158, 238);
}
.statsLi {
  display: inline-block;
  height: 98px;
  width: 120px;
  border: 1px solid gray;
}

.label {
  display: block;
  text-align: center;
  font-size: 24px;
  padding: 20px 0 14px 0;
  margin: 0;
}